.real-utils-feedback-pointer {
    min-width: 640px;

    .notice {
        margin: 5px 0 !important;

        > p {
            padding: 0 !important;
        }
    }

    fieldset {
        padding: 0 20px 10px;

        label {
            display: block;
            margin-top: 5px;

            &.col-2 {
                width: 50%;
                float: left;
            }
        }

        .regular-text {
            display: block;
            width: 100%;
            margin-bottom: 10px;
        }
    }

    h3:before {
        content: "\f488";
        top: 50%;
        margin-top: -16px;
    }

    .wp-pointer-arrow {
        left: 15px !important;
    }
}
